.coverSlider{display: block;height:calc(100vh - 48px);background: linear-gradient(-45deg,#cf11da,#3482fd);}
.coverImg{display:block; margin-top: -85px;}
.coverSlider .coverBody{display:flex;justify-content:center;height:100%;flex-direction:column}
.coverSlider .coverBody h5{color:#f7f5f5;font-size:22px;font-weight:600;text-transform:capitalize;margin-bottom:5px}
.coverSlider .coverBody h1{color:#fff;font-size:65px;font-weight:700;text-transform:capitalize;margin-bottom:25px;line-height:1.2}
.coverSlider .coverBody p{color:#f7f5f5;line-height:1.6;letter-spacing:.3px;margin-bottom:30px}
